import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import ChocoView from '@/components/ChocoView'

Vue.use(Router)

export default new Router({
  routes: [

    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/edit',
      name: 'ChocoView',
      component: ChocoView
    },
    {
      path: '/something',
      name: 'something',
      component: ChocoView
    }
  ],
  mode: 'history'
})



